import axios from "axios";

export const GET_LOKASI_LIST = "GET_LOKASI_LIST";
export const EDIT_LOKASI = "EDIT_LOKASI";

const server = process.env.REACT_APP_BACKEND_URL;
export const getLokasi = () => {
  return (dispatch) => {
    axios
      .get(server + "storages2?tipeData=LOKASI")
      .then((res) =>
        dispatch({
          type: GET_LOKASI_LIST,
          payload: {
            data: res.data,
            alert:
              "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
            error: false,
          },
        })
      )
      .catch((err) =>
        dispatch({
          type: GET_LOKASI_LIST,
          payload: {
            data: false,
            alert:
              "alert alert-danger alert-dismissible bg-danger text-white border-0 fade show",
            error: true,
          },
        })
      );
  };
};

export const editLokasi = (
  tipeData,
  kodeLokasi,
  namaLokasi,
  status,
  kodeExt
) => {
  return (dispatch) => {
    dispatch({
      type: EDIT_LOKASI,
      payload: {
        tipeData: tipeData,
        kodeLokasi: kodeLokasi,
        namaLokasi: namaLokasi,
        status: status,
        kodeExt: kodeExt,
      },
    });
  };
};
