import axios from "axios";

export const GET_SALESMAN_LIST = "GET_SALESMAN_LIST";
export const EDIT_SALESMAN = "EDIT_SALESMAN";

const server = process.env.REACT_APP_BACKEND_URL;
export const getsalesmanList = () => {
  return (dispatch) => {
    axios
      .get(server + "storages2?tipeData=SALES")
      .then((res) =>
        dispatch({
          type: GET_SALESMAN_LIST,
          payload: {
            data: res.data,
            alert:
              "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
            error: false,
          },
        })
      )
      .catch((err) =>
        dispatch({
          type: GET_SALESMAN_LIST,
          payload: {
            data: false,
            alert:
              "alert alert-danger alert-dismissible bg-danger text-white border-0 fade show",
            error: true,
          },
        })
      );
  };
};

export const editsalesmanList = (
  tipeData,
  kodeLokasi,
  namaLokasi,
  status,
  kodeExt
) => {
  return (dispatch) => {
    dispatch({
      type: EDIT_SALESMAN,
      payload: {
        tipeData: tipeData,
        kodeLokasi: kodeLokasi,
        namaLokasi: namaLokasi,
        status: status,
        kodeExt: kodeExt,
      },
    });
  };
};
