import axios from 'axios'


export const GET_PEMBAYARANSUPPLIER_LIST = "GET_PEMBAYARANSUPPLIER_LIST"
export const SET_CIOK_TEMP = "SET_CIOK_TEMP"
export const SET_RONGSOK_TEMP = "SET_RONGSOK_TEMP"

const server = process.env.REACT_APP_BACKEND_URL;
export const getpembayaranSupplierList = () => {
    return (dispatch) => {
        axios
        .get(server + "subcategorys")
        .then((res) =>
          dispatch({
              type : GET_PEMBAYARANSUPPLIER_LIST,
              payload: {
                  data: res.data,
                  alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
                  error: false
              }
          })
        )
        .catch((err) =>
        dispatch({
            type : GET_PEMBAYARANSUPPLIER_LIST,
            payload: {
                data: false,
                alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade show",
                error: true
            }
        })
        )
    }
}

export const setTampungCiok = () => {
    var sum =0;
    var sum2 =0;
    var total = JSON.parse(localStorage.getItem("ciokTemp")) ? JSON.parse(localStorage.getItem("ciokTemp")) : "kosong" ;
    if(total === "kosong"){
        sum = 0
    } else {
        sum = total.map(hasil => parseFloat(hasil.netto)).reduce((acc, hasil) => acc + hasil);
        localStorage.setItem("totalCiok", sum)
        sum2 = total.map(hasil => parseFloat(hasil.bruto)).reduce((acc, hasil) => acc + hasil);
        localStorage.setItem("totalBrutoCiok", sum2)
    }
    return (dispatch) => {
         
       dispatch({
           type: SET_CIOK_TEMP,
        payload: {
            data: JSON.parse(localStorage.getItem("ciokTemp")) || [],
            totalNetto : sum,
            totalBruto : sum2,
            alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
            error: false
        }
       })
    }
}

export const settampungRongsok = () => {
    var total = JSON.parse(localStorage.getItem("rongsokTemp")) ? JSON.parse(localStorage.getItem("rongsokTemp")) : "kosong" ;
    var sum = 0;
    var sum2 = 0;
    if(total === "kosong"){
        sum = 0;
    } else {
        sum = total.map(hasil => parseFloat(hasil.netto)).reduce((acc, hasil) => acc + hasil);
        localStorage.setItem("totalRongsok", sum);
        sum2 = total.map(hasil => parseFloat(hasil.bruto)).reduce((acc, hasil) => acc + hasil);
        localStorage.setItem("totalBrutoRongsok", sum2);
    }
    
    return (dispatch) => {
        dispatch({
            type: SET_RONGSOK_TEMP,
            payload: {
                data: JSON.parse(localStorage.getItem("rongsokTemp")) || [],
                 totalNetto : sum,
                 totalBruto: sum2,
                alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
                error: false
            }
           })
    }
}

