import Axios from "axios";

export const GET_VALIDASI_BARANG_PINDAH_BARANG = "GET_VALIDASI_BARANG_PINDAH_BARANG";
export const GET_VALIDASI_BARANG_MUTASI_BARANG = "GET_VALIDASI_BARANG_MUTASI_BARANG";
export const GET_VALIDASI_BARANG_MUTASI_LK = "GET_VALIDASI_BARANG_MUTASI_LK";
export const GET_VALIDASI_BARANG_KIRIM_MASAK = "GET_VALIDASI_BARANG_KIRIM_MASAK";
export const GET_VALIDASI_BARANG_TERIMA_MASAK = "GET_VALIDASI_BARANG_TERIMA_MASAK";


const server = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
export const getValidasiBarangPindahBarang = () => {
    return (dispatch) => {
        Axios
            .get(server + "item/dc/pindahbarang/heads?statusValid=OPEN")
            .then((res) =>
                dispatch({
                    type: GET_VALIDASI_BARANG_PINDAH_BARANG,
                    payload: {
                        data: res.data,
                        alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
                        error: false
                    }
                })
            )
            .catch(() =>
                dispatch({
                    type: GET_VALIDASI_BARANG_PINDAH_BARANG,
                    payload: {
                        data: false,
                        alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade show",
                        error: true
                    }
                })
            )
    }
}
